<template>
    <view class="pickerBackground">
 
		
        <!-- <view class="uni-padding-wrap">
            <view class="uni-title">日期：{{year}}年{{month}}月{{day}}日</view>
        </view> -->
		<view class="disRowAround uni-padding-wrap">
			<view class="disRowAroundL" @click="closePicker">
					取消
			</view>  
			<view class="disRowAroundR" @click="okPicker">
					确定
			</view>
		</view>
        <picker-view v-if="visible" :indicator-style="indicatorStyle" :value="value" @change="bindChange" class="picker-view">
            <picker-view-column>
                <view class="item" v-for="(item,index) in years" :key="index">{{item}}</view>
            </picker-view-column>
            <picker-view-column>
                <view class="item" v-for="(item,index) in months" :key="index">{{item}}</view>
            </picker-view-column>
            <picker-view-column>
                <view class="item" v-for="(item,index) in days" :key="index">{{item}}</view>
            </picker-view-column>
        </picker-view>
   
   </view>
	<!-- <uni-popup ref="popup" type="message">
		<uni-popup-message type="error" message="请输入正确的手机号码" ></uni-popup-message> 
	</uni-popup> -->
</template>
<script>
	 
    export default {
		props:['visible'],
		components:{},
        data: function () {
            const date = new Date()
            const years = []
            const year = date.getFullYear()
            const months = []
            const month = date.getMonth() + 1
            const days = []
            const day = date.getDate()
            for (let i = 1990; i <= date.getFullYear(); i++) {
                years.push(i)
            }
            for (let i = 1; i <= 12; i++) {
                months.push(i)
            }
            for (let i = 1; i <= 31; i++) {
                days.push(i)
            }
            return {
                title: 'picker-view',
                years,
                year,
                months,
                month,
                days,
                day,
                value: [9999, month - 1, day - 1],
                
                indicatorStyle: `height: 50px;`
            }
        },
        methods: {
			 closePicker(){
				 this.$emit("noPicker")
			 },  
			 okPicker(){
				 let birthdayStr = this.year +'/'+  this.month +'/'+ this.day 
			      this.$emit("okPicker",birthdayStr)
			 },
            bindChange: function (e) {
                const val = e.detail.value
                this.year = this.years[val[0]]
                this.month = this.months[val[1]]
                this.day = this.days[val[2]]
            }
        }
    }
</script>
<style>
	.pickerBackground{
		background-color: #fff;
		height: 600rpx;
	}
    .picker-view {
        width: 750rpx;
        height: 600rpx;
        margin-top: 20rpx;
    }
    .item {
        height: 50px;
        align-items: center;
        justify-content: center;
        text-align: center;
    }
	.disRowAroundL{
		color: #3880ff;
		color: #3880ff;
		
	}
	.uni-padding-wrap{
		padding: 20upx;
	}
	.disRowAroundR{
		color: #3880ff;
		font-size: 13px;
		font-weight: bold;
	}
</style>